<template>
<v-container>
<v-row>
  <v-col cols="12" md="4">
    <div>
   <h3 class="text-center">Roles</h3>
    <v-card :disabled="Change" v-for="(item,index) in roles" :key="index" link  @click="SelectRol(item.descripcion)" class="pl-2 pa-2 mb-1 d-flex align-center justify-space-between">
       <div class="subtitle-1 font-weight-bold">{{item.descripcion}}</div>
      <v-card v-if="item.value"
      color="red darken-4"
      elevation="1"
      height=30
      rounded
      width="30"
      ></v-card>
    </v-card>
     <div>
      <h4> Seleccionado: <span class="font-weight-bold red--text text-darken-4">{{UsuarioPendiente.nombre}}</span></h4>
      <h4> Rol: <span v-if="(UsuarioPendiente.nombre).trim()" class="font-weight-bold red--text text-darken-4">{{rolUsuarioPendiente}}</span></h4>
     </div>
     <v-btn v-if="(UsuarioPendiente.nombre).trim()" :disabled="btnGuardar" class="my-5" block dark color="red darken-4" @click="ChangeRolUser">
              Guardar
     </v-btn>
      <v-alert v-if="registroRolHecho" dense class="text-center" type="success">
          Modificaci√≥n Exitosa</v-alert>
     </div>
  </v-col>
 <v-col cols="12" md="6">
   <v-simple-table 
    dark
    fixed-header
   class="my-2" 
   height="400px"
   >
     <template v-slot:default>
        <thead>
          <tr>
          <th  class="red darken-4 text-center subtitle-1 font-weight-bold white--text " style="border-top-left-radius:5px;border-bottom-left-radius:5px" >
            Nombres
          </th>
          <th class="red darken-4 text-center subtitle-1 font-weight-bold white--text " style="border-top-right-radius:5px;border-bottom-right-radius:5px">
            Email
          </th>
          </tr>
        </thead>
            <tbody >
              <tr v-for="item in Usuarios" :key="item.idusuario"  class="font-weight-bold "  @click="SelectUsuario(item.idusuario,item.usuario,item.rol)" >
                <td class="text-center">{{item.usuario}}</td>
                <td class="text-center red--text text-darken-4 ">{{item.email}}</td>
              </tr>
            </tbody>
     </template>
   </v-simple-table>
  
 </v-col>
</v-row>

</v-container>
</template>
<script>
import {mapState} from 'vuex'
  export default {
    name:'forms',
    data: () => ({
      registroRolHecho:false,
      btnGuardar:true,
      UsuarioPendiente:{id:'',nombre:''},
      rolUsuarioPendiente:'',
      Change:false,
      Usuarios:[],
      roles:[],
      show1:false,
      selectedItem: 1,
      items: [
        { text: 'Real-Time', icon: 'mdi-clock' },
        { text: 'Audience', icon: 'mdi-account' },
        { text: 'Conversions', icon: 'mdi-flag' },
      ],
    }),
    computed:{
        ...mapState(['token'])
    },
    mounted(){
      this.listarUsuario()
      this.listarRol()
    },
    methods: {
      listarUsuario(){
          this.axios.get('/listarUsuario')
          .then(res=>{
            this.Usuarios=res.data
          })
          .catch(err=>console.log(err))
      },
       listarRol(){
          this.axios.get('/listarRol')
          .then(res=>{
            res.data.map(e=>{
              this.roles.push({descripcion:e.descripcion,value:false})
            })
          })
          .catch(err=>console.log(err))
      },
      SelectRol(item){
        this.btnGuardar=false;
        this.Change=true;
        this.roles.map( ele =>{ 
          if  (item===ele.descripcion )
              { 
                this.rolUsuarioPendiente=item;
                ele.value=true 
                }
          else
              { ele.value=false}
              }
        )        
        this.Change=false;
    },
    SelectUsuario(idusuario,usu,rol){
      this.UsuarioPendiente.id=idusuario;
      this.UsuarioPendiente.nombre=usu;
      this.SelectRol(rol);
    },
    ChangeRolUser(){
      var headers = {
        "Content-Type": "application/json",
        token: this.token,
      };
      console.log('peticion');
        if(this.UsuarioPendiente.id>0  && this.rolUsuarioPendiente){
          this.axios.post("/saveRoles",{
            idusuario:this.UsuarioPendiente.id,
            rol:this.rolUsuarioPendiente
          },{headers})
          .then(res =>{
        
            if(res.data.mensaje==='Registro Exitoso'){
            this.listarUsuario()
            this.ShowMessageRegisterRol();
            }
            
            
          })
          .catch(err=> console.log(err.response.data))

        }
    },
     ShowMessageRegisterRol(){
     this.registroRolHecho=true;
      setTimeout(()=>{this.registroRolHecho=false}, 3000);
    }
    },


  }

</script>